<template>
  <div class="select">
    <div class="flex">
      <input @click="visible=true" required type="text" :value="text" placeholder="Category" readonly class="cursor-pointer">
      <div class="input-icon">
        <img src="@/assets/icon/chevron_down.svg" alt="icon" width="25">
      </div>
    </div>
    <ul v-show="visible" class="dropdown">
      <li 
        @click="selected(item)" 
        v-for="item in data" :key="item.name"
        class="capitalize">
        {{ item.name }}
      </li>
      <!-- <li>
        <input type="text" v-model="other" @keypress.enter="selected(other)" placeholder="Others" class="border border-gray mb-0">
      </li> -->
    </ul>
  </div>
</template>

<script>
export default {
  props: ['data', 'categories_id'],
  data: () => ({
    visible: false,
    text: '',
    other: ''
  }),
  methods: {
    handleClickOutside(evt) {
      if (!this.$el.contains(evt.target)) {
        this.visible = false;
      }
    },
    selected(data) {
      this.$emit('update:categories_id', data.id)
      // this.categories_id = data.id
      this.text = data.name
      this.visible = false
      this.other = ''
    }
  },
  mounted() {
    document.addEventListener('click', this.handleClickOutside);
  },
  unmounted() {
    document.removeEventListener('click', this.handleClickOutside);
  },
}
</script>

<style scoped>
.select{
  @apply relative;
}
input{
  @apply appearance-none w-full px-3 py-2 mb-4 resize-y;
}
input:focus{
  @apply outline-none;
}
.input-icon{
  width: 40px;
  height: 40px;
  @apply flex justify-center items-center bg-white;
}
.dropdown{
  @apply absolute w-full bg-white rounded shadow top-0 py-2;
}
li{
  @apply py-2 px-3 cursor-pointer;
}
li:hover{
  @apply bg-gray-200;
}
</style>