<template>
  <div class="mb-10">
    <p class="mb-3 font-bold">MESSAGE</p>
    <form @submit.prevent="form">
      <input type="text" v-model="data.name" placeholder="Name" autofocus required>
      <input type="number" v-model="data.telepon" placeholder="Number Phone" required>
      <input type="email" v-model="data.email" placeholder="Email" required>
      <Select v-if="data.categories_id!=2" :data="categories" v-model:categories_id="data.categories_id"/>
      <div v-else>
        <span>Please write the category</span>
        <input type="text" v-model="data.category_lainnya" placeholder="Other category" required>      
      </div>
      <textarea v-model="data.detail" placeholder="Message" rows="4" required></textarea>
      <div 
        v-show="status!=null"
        class="p-4 mb-4 text-white"
        :class="{
          'bg-red-600': status==false?true:false,
          'bg-green-600': status==true?true:false
        }">
        {{ msg }}
      </div>
      <div class="text-right">
        <button :disabled="loading" type="submit" class="btn btn-home btn-lg inline-block">
          <svg 
            v-if="loading"
            class="animate-spin text-white" style="width: 24px;height: 24px;" xmlns="http://www.w3.org/2000/svg" fill="none">
            <circle class="opacity-25" cx="12px" cy="12px" r="10" stroke="currentColor" stroke-width="4"></circle>
            <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          <span v-else>send</span>
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import Select from './Select'
import http from '@/config/http'
export default {
  components: {
    Select
  },
  data: () => ({
    data: {
      name: '',
      telepon: '',
      email: '',
      categories_id: '',
      detail: '',
      // category_lainnya: ''
    },
    status: null,
    msg: '',
    loading: false,
    categories: []
  }),
  watch: {
    status() {
      this.autoclose()
    }
  },
  methods: {
    getData: async function () {
      await http.get('inquiry-category')
        .then(res => {
          this.categories = res.data.data
        })
    },
    form() {
      this.loading = true
      http.post('inquiry', this.data)
        .then(res => {
          if (res.status == 200) {
            this.status = true
            this.msg = 'Success'
          } else {
            this.msg = 'Error'
            this.status = false
          }
          this.loading = false
        })
        .catch(() => {
          this.msg = 'Error'
          this.status = false
          this.loading = false
        })
    },
    autoclose: function () {
      const vm = this
      setTimeout(() => {
        vm.status = null
        vm.msg = ''
      }, 3000)
    }
  },
  created() {
    this.getData()
  }
}
</script>

<style scoped>
input, textarea{
  @apply appearance-none w-full px-3 py-2 mb-4 resize-y;
}
input, textarea:focus{
  @apply outline-none;
}
</style>